.blogs-box {
  padding: 100px 0;
  display: flex;
  justify-content: space-between;
}

.blogs-box .top_blog {
  width: calc((100% - 40px) / 2);
  padding: 30px;
  background-color: #fff;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
}

.blogs-box .top_blog .ttl-Box .ttl {
  padding-bottom: 5px;
  color: #333;
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 1.6em;
  letter-spacing: 0.1em;
  border-bottom: 3px solid #ccc;
  position: relative;
}

.blogs-box .top_blog .ttl-Box .ttl::after {
  content: '';
  display: block;
  width: 160px;
  height: 4px;
  background-color: #e45213;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.blogs-box .top_blog .scroll_box {
  box-sizing: border-box;
  overflow-y: scroll;
  transform: translateZ(0);
  height: 267px;
}

@media screen and (max-width: 767px) {
  .blogs-box {
    padding: 10.66667vw 0;
    display: block;
  }
  .blogs-box .top_blog {
    width: 100%;
    margin-bottom: 5.33333vw;
    padding: 5.33333vw;
    box-shadow: 0.8vw 0.8vw 2.66667vw rgba(0, 0, 0, 0.3);
  }
  .blogs-box .top_blog:last-of-type {
    margin-bottom: 0;
  }
  .blogs-box .top_blog .ttl-Box .ttl {
    padding-bottom: 1.33333vw;
    font-size: 2.2rem;
  }
  .blogs-box .top_blog .ttl-Box .ttl::after {
    width: 37.33333vw;
  }
  .blogs-box .top_blog .scroll_box {
    height: 100vw;
  }
}
